.App {
  text-align: center;
}

.App-header {
  text-align: center;
  background-color: #222;
  padding: 1px;
  color: white;
}

.App-title {
  font-size: 15px;
}

.App-intro {
  font-size: large;
}

.Div-border-red{
  border-style: dashed;
  border-width: 2px;
  border-color: red;
}

.Div-border-yellow{
  border-style: dashed;
  border-width: 2px;
  border-color: yellow;
}

.Div-border-blue{
  border-style: dashed;
  border-width: 2px;
  border-color: blue;
}

.Div-border-green{
  border-style: dashed;
  border-width: 2px;
  border-color: green;
}

.Div-side1{
  float:left;
  width:35%;
  min-width: 500px;
}
.Div-side2{
  float:left;
  width:35%;
  min-width: 400px;
}
.Div-side3{
  float:left;
  width:18%;
  min-width: 200px;
}

.Div-scroll{
  height: 350px;
  overflow-y: auto;
}
.Div-bg-color-orangeish{
  background-color: rgb(255, 255, 255);
}
.Div-bg-color-yellowish{
  background-color: rgb(253, 255, 131);
}
.Div-bg-color-greenish{
  background-color: rgb(119, 255, 111);
}

.rejt-name{
  color: chartreuse;
  font-weight: bold;
}

.rejt-collapsed {
  cursor: pointer;
}

.rejt-value{
  cursor: pointer;
}

.rejt-minus-menu{
  cursor: pointer;
}

.rejt-plus-menu{
  cursor: pointer;
}

.Font-size15px{
  font-size: 15px;
}


